import React, {useState} from 'react';
import {NavLink} from 'react-router-dom';

const Menu = () => {
    const [menuActive, setMenuActive] = useState(false);
    function toggleMenuVisibility() {
        setMenuActive(!menuActive);
    }
    const links = [
        {to: '/', title: 'Main'},
        {to: '/about', title: 'About'},
        {to: '/skills', title: 'Skills'},
        {to: '/works', title: 'Works'},
        {to: '/contact', title: 'Contact'}
        
    ];
    const menuLinks = links.map(item => {
        const {to, title} = item;
        return (
            <li className="menu__item" key={title}>
                <NavLink
                    className="menu__link"
                    activeClassName="menu__link_active"
                    to={to}
                    exact
                    onClick={() => setMenuActive(false)}
                >
                    {title}
                </NavLink>
            </li>
        );
    });

    return (
        <>
            <div 
                className="hamburger"
                onClick={toggleMenuVisibility}
            >
                <span></span>
                <span></span>
                <span></span>
            </div>
            <div className={`menu ${menuActive ? 'menu_active' : ''}`}>
                <div className="menu__close" onClick={toggleMenuVisibility}>&times;</div>
                <nav>
                    <div className="container">
                            <ul className="menu__list">
                                {menuLinks}
                            </ul>
                    </div>
                </nav>
                <div className="menu__social">
                    <a href="https://www.facebook.com/people/%D0%90%D0%BD%D1%82%D0%BE%D0%BD-%D0%93%D0%BE%D1%80%D0%B1%D1%83%D0%BD%D0%BE%D0%B2/100059210825402/"><img src="https://ucarecdn.com/929db7cd-7829-48a0-9467-f4395428953f/facebook.png" alt="social"/></a>
                    <a href="https://github.com/anton-gorbunov"><img src="https://ucarecdn.com/c0d2e68a-c0ba-4c7e-bba0-52cebb271f72/github.png" alt="social"/></a>
                    <a href="https://t.me/anton_gorbunov06"><img src="https://ucarecdn.com/d5411885-e1c8-4bfc-9ecb-748ca18a8671/telegram.png" alt="social"/></a>
                </div>
            </div>
        </>
        
    );

};

export default Menu;